<template>
    <div class="d-flex justify-content-between align-items-center py-2">
        <div class="d-flex flex-row bd-highlight" v-if="binding==='d'">
            <div class="bd-highlight">
                <img :src="$store.state.imgurl+'/assets/img/userimg/'+show.avatar+'.jpg'" width="50" height="50" class="rounded-pill border border-2 border-white">
            </div>
            <div class="bd-highlight ms-2">
                <div class="fw-bold fs-6">{{show.nickname}}</div>
                <div>{{show.pevaluate}} 分 - 出行<span class="text-primary">{{show.pint}}</span>次</div>
            </div>
        </div>
        <div class="bd-highlight" v-else>
          <div class="d-flex flex-row align-items-center">
            <div class="bd-highlight position-relative stretched-link" @click="$tourl('user-'+show.duser_id)"><img :src="show.imgurl" width="50" height="50" class="rounded-pill border border-2 border-white"></div>
            <div class="bd-highlight ms-1">
              <div class="fw-bold fs-6">{{show.brand}}</div>
              <div><span>{{show.typeinfo}} {{show.colour}}</span><span class="ms-1">{{show.devaluate.toFixed(1)}}分</span><span class="ms-1">接单{{show.dint}}次</span></div>
            </div>
          </div>
        </div>
        <div class="bd-highlight">
            <com-trip-mobile :mobile='show.mobile' :binding="binding" :state="show.state"/>
            <com-trip-chat :url="'chat-'+getid()+'-'+(binding==='p' && show.state==='1'?'-'+show.id:show.id)" :binding="binding" :state="show.state" :data="data" :chatname="chatname()"/>
        </div>
    </div>
</template>

<script>
import Iconspill from "../../block/icons/pill-icons";
export default {
  components: {
    Iconspill,
  },
  props: {
    binding:'',
    set:{},
    data:{},
    show:{},
  },
  methods:{
    chatname(){
      let sid,tid
       sid=this.data.userinfo.user_id;
       tid=this.getid();
      return sid>tid?tid+','+sid:sid+','+tid;
    },
    getid(){
      if(this.binding==='p'){
        if(this.show.state==='1'){
          return this.show.user_id;
        }else{
          return this.show.duser_id;
        }
      }else if(this.binding==='d'){
       return this.show.user_id;
      }
    }
  }
}
</script>



