import axios from 'axios'
export default {
    reload(data) {
        this.data = Object.assign({});
        this.$todd(data).then(data => {
            this.data = data;
        });
        // let info = {};
        // for (let i in data) {
        //     info[i] = data[i];
        // }
        // for (let i in data.data) {
        //     if (typeof data.data[i] === 'object') {
        //         for (let x in data.data[i]) {
        //             this.$set(this.data.data[i], x, data.data[i][x]);
        //         }
        //     }
        // }
        // this.data = Object.assign({}, this.form, info);
        // this.$forceUpdate();
    },
    ispathMatch() {
        let patharr = this.$route.path.split('/');
        for (let i = 0; i < patharr.length; i++) {
            if (patharr[i] == "" || typeof(patharr[i]) == "undefined") {
                patharr.splice(i, 1);
                i = i - 1;
            }
        }
        this.viewkey = patharr.pop();
        this.ispathaxios();
    },
    ispathaxios() {
        if (this.$store.state.datacache[this.viewkey]) {
            if (this.$store.state.datacache[this.viewkey].cache === 1) {
                this.setwebdata();
            } else if (this.$store.state.datacache[this.viewkey].cache === 2 && this.$store.state.datacache_name.indexOf(this.viewkey) > -1) {
                this.setwebdata();
                this.get_pathaxios(this.viewkey);
            } else if (this.$store.state.datacache[this.viewkey].cache === 3) {
                this.setwebdata();
                this.get_pathaxios(this.viewkey);
            } else {
                this.pathaxios(this.viewkey);
            }
        } else {
            this.pathaxios(this.viewkey);
        }
    },
    setwebdata() {
        this.data = {};
        if (this.$store.state.datacache[this.viewkey].data) {
            for (let x in this.$store.state.datacache[this.viewkey].data) {
                this.$set(this.data, x, this.$store.state.datacache[this.viewkey].data[x]);
            }
        }
    },
    pathaxios(viewkey) {
        if (this.loading == false) {
            this.loading = true;
            axios.get(this.$store.state.apiset.url + '/index/layout/class/' + viewkey + '?token=' + this.$store.state.token)
                .then(response => {
                    this.loading = false;
                    let res = response.data;
                    if (res.code == 1) {
                        if (res.data) {
                            this.$store.commit('set_datacache', { name: viewkey, data: { cache: res.data.cache, data: res.data } });
                            this.$store.commit('set_datacache_name', viewkey);
                            this.data = res.data;
                            if (this.data.switch === 1) {
                                this.$store.commit('set_webrefresh', this.data.switch);
                                this.onrefresh(viewkey);
                            }
                        } else {
                            this.$router.push('/passenger');
                        }
                        // console.log(res.msg);
                    } else if (res.code == 401) {
                        this.$tourl('login');
                        this.$toast({ type: 'result', data: res });
                    } else if (res.code == 403) {
                        this.$tourl('login');
                        this.$toast({ type: 'result', data: res });
                    } else if (res.code == 6) {
                        this.$tourl('./');
                    } else if (res.code == 7) {
                        this.$tourl('./');
                        this.$toast({ type: 'result', data: res });
                    } else {
                        console.log(res.msg);
                    }
                }).catch(function(error) {
                    this.loading = false;
                    console.log(error)
                })
        }
    },
    get_pathaxios(viewkey) {
        axios.get(this.$store.state.apiset.url + '/index/layout/class/' + viewkey + '?token=' + this.$store.state.token)
            .then(response => {
                let res = response.data;
                if (res.code === 1 && res.data && viewkey === this.viewkey) {
                    this.$store.commit('set_datacache', { name: viewkey, data: { cache: res.data.cache, data: res.data } });
                    this.$store.commit('set_datacache_name', viewkey);
                    this.reload(res.data);
                }
            }).catch(function(error) {
                console.log(error)
            })
    },
    toast_refresh() {
        axios.get(this.$store.state.apiset.url + '/index/toastrefresh/?token=' + this.$store.state.token)
            .then(response => {
                if (response.data.code === 1) {
                    if (response.data.msg === 1) {
                        axios.get(this.$store.state.apiset.url + '/index/layout/class/' + this.viewkey + '?token=' + this.$store.state.token).then(response => {
                            if (response.data.code == 1 && response.data.data) {
                                this.data = response.data.data;
                            }
                        }).catch(function(error) {
                            console.log(error)
                        })
                    }
                    setTimeout(() => {
                        this.toast_refresh();
                    }, 300)
                }
            }).catch(function(error) {
                console.log(error)
            })
    },
    onrefresh(viewkey) {
        axios.get(this.$store.state.apiset.url + '/index/layout/class/' + viewkey + '?token=' + this.$store.state.token)
            .then(response => {
                let res = response.data;
                if (res.data) {
                    setTimeout(() => {
                        if (this.data.switch === 1) {
                            if (this.loading === false && viewkey === this.viewkey) {
                                if (this.$store.state.webrefresh === 1) {
                                    this.data = res.data;
                                }
                                this.onrefresh(viewkey);
                            }
                        }
                    }, 300)
                }
            }).catch(function(error) {
                console.log(error)
            })
    },
    onsubmit(key) {
        this.loading = true;
        key.url = this.$store.state.apiset.url + key.url;
        if (!key.params) {
            key.params = {};
        }
        key.params.token = this.$store.state.token;
        axios(key).then(response => {
            this.loading = false;
            let res = response.data;
            if (res.code == 1) {
                if (res.data) {
                    if (res.data.userinfo) {
                        this.handle_userinfo(res);
                    }
                    if (res.data.go) {
                        this.$tourl('./');
                    }
                    console.log(res);
                } else {
                    console.log(res);
                    // this.$router.push('/passenger');
                }
            } else if (res.code == 5) {
                this.$toast({ type: 'result', data: res });
                if (this.$store.state.webrefresh === 1) {
                    this.$store.commit('set_webrefresh', 0);
                    setTimeout(() => { this.$store.commit('set_webrefresh', 1); }, 20000);
                }
                this.get_pathaxios(this.viewkey);
            } else if (res.code == 6) {
                this.$tourl('./');
            } else if (res.code == 701) {
                localStorage.setItem('token', '');
                this.$store.commit('set_datacache_name', '');
                this.$tourl('/');
            } else if (res.code == 72) {
                if (res.data.url) {
                    this.$tourl(res.data.url);
                }
                this.$toast({ type: 'result', data: res });
            } else if (res.code == 62) {
                for (let x in res.data) {
                    for (let e in res.data[x]) {
                        this.data.data[x][e] = res.data[x][e];
                    }
                }
                if (this.$store.state.webrefresh === 1) {
                    this.$store.commit('set_webrefresh', 0);
                    setTimeout(() => { this.$store.commit('set_webrefresh', 1); }, 20000);
                }
                this.$toast({ type: 'result', data: res });
            } else {
                this.$toast({ type: 'result', data: res });
            }
        }).catch(function(error) {
            this.loading = false;
            console.log(error)
        })
    },
    handle_userinfo(res) {
        const token = res.data.userinfo.token;
        const user = res.data.userinfo;
        localStorage.setItem('token', token)
            // 每次请求接口时，需要在headers添加对应的Token验证
        axios.defaults.headers.common['Authorization'] = token
            // 更新token
        this.$store.commit('auth_success', token, user);
        let pathurl = this.$route.path;
        pathurl = pathurl.replace(RegExp("/login", "g"), '');
        this.$router.push(pathurl);
    }
}


